/* General Reset */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}


.header {
    display: flex;
    justify-content: space-between; 
    align-items: center;
    margin: 10px;
    padding: 10px;
    background-color: #f0f0f0;
    
}

.nav-items {
    display: flex; 
    float: right;
}

.nav-items > ul {
    list-style-type: none; 
    display: flex; 
    margin: 0; 
    padding: 0; 
    
}
.nav-items > ul > li {
    padding: 10px 20px; 
    cursor: pointer;
}

.nav-items > ul > li:hover {
    background-color: #ddd;
    border-radius: 5px;
}


.nav-items > ul > li.active {
    background-color: #ccc;
    border-radius: 5px;
    font-weight: bold;
}
.logo{
    font-size: 1.5em;
    font-weight: bold;
    color: #ccc;
}